import React from 'react'
import {Link, graphql, useStaticQuery} from 'gatsby'
import BreadcrumContent from '../../../../Global/Pages/BreadcrumbContent.component'
const ShopCart=()=>{

    const data = useStaticQuery(graphql`
query{
    close: file(relativePath: {eq: "shop/close.png"}) {
        id
        childImageSharp {
          fluid {
            originalImg
          }
        }
    }
    cart1: file(relativePath: {eq: "shop/cart1.png"}) {
        id
        childImageSharp {
          fluid {
            originalImg
          }
        }
    }
    cart2: file(relativePath: {eq: "shop/cart2.png"}) {
        id
        childImageSharp {
          fluid {
            originalImg
          }
        }
    }
    cart3: file(relativePath: {eq: "shop/cart3.png"}) {
        id
        childImageSharp {
          fluid {
            originalImg
          }
        }
    }
}`)
    
    return(
        <section className="shop-checkouts bgc-f7">
            <div className="container">
                <div className="row">
                    <div className="col-xl-6">
                        <BreadcrumContent title='Cart' active='Cart'/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-xl-12">
                        <div className="cart_page_form">
                            <form action="#">
                                <table className="table table-responsive">
                                    <thead>
                                        <tr className="carttable_row">
                                            <th className="cartm_title">Product</th>
                                            <th className="cartm_title">Price</th>
                                            <th className="cartm_title">Quantity</th>
                                            <th className="cartm_title">Total</th>
                                            <th className="cartm_title"></th>
                                        </tr>
                                    </thead>
                                    <tbody className="table_body">
                                        <tr>
                                            <th scope="row">
                                                <ul className="cart_list">
                                                    <li className="list-inline-item pr20"><Link to="#"><img src={data.cart1.childImageSharp.fluid.originalImg} alt="cart1.png"/></Link></li>
                                                    <li className="list-inline-item"><Link className="cart_title" to="#">Album</Link></li>
                                                </ul>
                                            </th>
                                            <td className="text-thm">$13,000</td>
                                            <td><input className="cart_count text-center" placeholder="5" type="number"/></td>
                                            <td className="cart_total text-thm">$194,00</td>
                                            <td><Link to="#"><img src={data.close.childImageSharp.fluid.originalImg} alt="close.png"/></Link></td>
                                        </tr>
                                        <tr>
                                            <th scope="row">
                                                <ul className="cart_list">
                                                    <li className="list-inline-item pr20"><Link to="#"><img src={data.cart2.childImageSharp.fluid.originalImg} alt="cart2.png"/></Link></li>
                                                    <li className="list-inline-item"><Link className="cart_title" to="#">Beanie</Link></li>
                                                </ul>
                                            </th>
                                            <td className="text-thm">$13,000</td>
                                            <td><input className="cart_count text-center" placeholder="5" type="number"/></td>
                                            <td className="cart_total text-thm">$194,00</td>
                                            <td><Link to="#"><img src={data.close.childImageSharp.fluid.originalImg} alt="close.png"/></Link></td>
                                        </tr>
                                        <tr>
                                            <th scope="row">
                                                <ul className="cart_list">
                                                    <li className="list-inline-item pr20"><Link to="#"><img src={data.cart3.childImageSharp.fluid.originalImg} alt="cart3.png"/></Link></li>
                                                    <li className="list-inline-item"><Link className="cart_title" to="#">Hoodie</Link></li>
                                                </ul>
                                            </th>
                                            <td className="text-thm">$13,000</td>
                                            <td><input className="cart_count text-center" placeholder="5" type="number"/></td>
                                            <td className="cart_total text-thm">$194,00</td>
                                            <td><Link to="#"><img src={data.close.childImageSharp.fluid.originalImg} alt="close.png"/></Link></td>
                                        </tr>
                                    </tbody>
                                </table>
                            </form>
                            <div className="checkout_form mt30">
                                <div className="checkout_coupon ui_kit_button">
                                    <form className="form-inline">
                                        <input className="form-control coupon_input" type="search" placeholder="Coupon Code" aria-label="Search"/>
                                        <button type="button" className="btn btn2">Apply Coupon</button>
                                        <button type="button" className="btn btn3">Update Cart</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 offset-lg-7">
                        <div className="order_sidebar_widget style2">
                            <h4 className="title">Cart Totals</h4>
                            <ul>
                                <li className="subtitle"><p>Subtotal <span className="float-right">$13,000</span></p></li>
                                <li className="subtitle"><p>Total <span className="float-right totals color-orose">$943,00</span></p></li>
                            </ul>
                            <div className="ui_kit_button payment_widget_btn">
                                <button type="button" className="btn dbxshad btn-lg btn-thm btn-block">Proceed to checkout</button>
                            </div>
                        </div>
				</div>
                </div>
            </div>
        </section>
    )
}
export default ShopCart